body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 2.5rem;
}
.signUpBtn {
    padding: 0 3px;
    background-color: #ebebeb;
    border: 2px solid black;
    text-decoration: none;
    box-shadow: 3px 3px rgba(0,0,0,0.75);
}

form {
    width: 400px;
    display: flex;
    flex-direction: column;
}

form input,
form button {
    padding:5px;
    font-size: 1rem;
    margin-bottom: 10px;
}

form p {
    color: red;
}
.navigation {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    list-style-type: none;
    align-items: center;
}

.navigation li {
    padding-left: 10px;
}
.upcontainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.userLink {
    color: blue;
    text-decoration: underline;
}

.userLink:hover {
    color: violet;
}
.activelike {
    color: blue;
    text-decoration: underline;
}

.activelike:hover {
    color: violet;
    text-decoration: underline;
}
.card {
    width: 70%;
    border: 3px solid black;
    padding: 10px 20px;
    margin-bottom: 10px;
}

.card .description {
    display:flex;
    justify-content: space-between;
}

.card .description p {
    padding-left: 20px;
}

.card .description img{
   max-width: 100%;
   height: 250px;
}

.card .likeBar {
    display:flex;
    justify-content: space-between;
}

.card .activelike {
    color: blue;
    text-decoration: underline;
}

.card .activelike:hover {
    color: violet;
    text-decoration: underline;
}
form textarea {
    height: 150px;
    padding:5px;
    font-size: 1rem;
    margin-bottom: 10px;
}

.houseFormLink {
    font-size: 2rem;
    padding: 0 3px;
    background-color: #ebebeb;
    border: 2px solid black;
    text-decoration: none;
    box-shadow: 3px 3px rgba(0,0,0,0.75);
}
.filters {
    padding: 5px;
    border: 2px solid black;
    margin-bottom:20px;
}

.filters button {
        background:none!important;
        color:blue;
        border:none; 
        padding:0!important;
        font: inherit;
        /*border is optional*/
        border-bottom:1px solid #444; 
        cursor: pointer;
}
.newHouseDiv {
    position: absolute;
    right: 10px;
}
.app {
    padding: 5px 10px;
}



